import { UPLOAD_FILE_REQUEST, UPLOAD_FILE_SUCCESS, UPLOAD_FILE_ERROR } from '../actions/uploadFile'

const initialState = {  
    text: [],
    isLoading: false,
    isError: false, 
    err: ''
};


  
export function uploadFileReducer(state = initialState, action) {
    switch(action.type) {
        case UPLOAD_FILE_REQUEST:
            return { ...state, isLoading: true, isError: false }

        case UPLOAD_FILE_SUCCESS:
            return { ...state, isLoading: false, isError: false, text: action.payload.data }
    
        case UPLOAD_FILE_ERROR:
            return { ...state, isLoading: false, isError: true, err: action.payload.stack }

        default:
            return state;
    }
};


