import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import SideMenu from './sidemenu/main';
import Header from './header/main'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch
} from "react-router-dom";


const StyledWrapper = styled.section`
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
`;


const MainContent = styled.section`
  flex: 1; 
`;



function WrapperMP(props) {
  const { smOpened } = props;
  let { path, url } = useRouteMatch();
  const mainpath = 'main'
  return (
    <StyledWrapper>
        <SideMenu />
        <MainContent>
          <Header />
          <Switch>
            <Route exact path={path}>
              <div>Главная страница</div>
            </Route>
            <Route path={`${path}/calendar`}>
              <div>Календарь</div>
            </Route>
            <Route path={`${path}/rasp`}>
              <div>Расписание</div>
            </Route>
            <Route path={`${path}/rating`}>
              <div>рейтинг</div>
            </Route>
            <Route path={`${path}/workers`}>
              <div>Сотрудники тут</div>
            </Route>
            <Route path={`${path}/freeclasses`}>
              <div>Свои аудитории</div>
            </Route>
          </Switch>
        </MainContent>
    </StyledWrapper>
  );
}



WrapperMP.propTypes = {

}


const mapStateToProps = store => {
  return {

  }
}

export default connect(mapStateToProps)(WrapperMP)


