import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import SideMenuKKK from './sidemenu/mainkkk';
import Header from './header/main'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
  useLocation 
} from "react-router-dom";


const StyledWrapper = styled.section`
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
`;


const MainContent = styled.section`
  flex: 1; 
`;



function WrapperKKK(props) {
  const { smOpened } = props;
  let { path, url } = useRouteMatch();
  return (
    <StyledWrapper>
        <SideMenuKKK />
        <MainContent>
          <Header />
          <Switch>
            <Route exact path={path}>
                <div>Карта Креативных Компетенций</div>
            </Route>
            <Route path={`${path}/compеtence`}>
              <div>Компетенции</div>
            </Route>
            <Route path={`${path}/prog`}>
              <div>Программы</div>
            </Route>
            <Route path={`${path}/tags`}>
              <div>Теги</div>
            </Route>
          </Switch>
        </MainContent>
    </StyledWrapper>
  );
}



WrapperKKK.propTypes = {

}


const mapStateToProps = store => {
  return {

  }
}

export default connect(mapStateToProps)(WrapperKKK)


