import axios, { post } from 'axios'

export default function urlApi() {
    // const baseUrl = 'https://burgundyold.azurewebsites.net/';
    // const baseUrl = 'http://localhost:8085/';
    const baseUrl = process.env.REACT_APP_API_URL;
    const baseQueryLink = 'https://dev3.burgundy.ftbl.com/';
    return {

        getCountryInnerPage(id) {
            return axios.get(`${baseUrl}/api/Area/GetArea?id=${id}`)
        },

        postFile(file) {
            const formData = new FormData();
            formData.append('file',file)
            return post(`https://dev0.burgundy.ftbl.com/api/Vision`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            })
        }
    }
}


